import React, { useState,useEffect } from 'react';
import { getUserEmail } from "../utils/getUserEmail"; 
import { fetchSwymCsrfToken,fetchCsrfToken} from '../services/api/fetchCsrfService';
import { callwebService, getTenant } from "../utils/helpers";
import useToast from "../hooks/useToast"; 

 const  OutOfOffice = async ()=>{
  const { showSuccessToast, showErrorToast } = useToast();
  const [formData, setFormData] = useState({
    outOfOfficeUser: '',
    surrogateUser: '',
    startDate: '',
    endDate: ''
  });

  const tenant = await getTenant();
     const sandbox = tenant.match(/^https:\/\/([^-.]+)/)[1];
  useEffect(() => {
    getUserEmail().then((val)=>{
      setFormData(prevState => ({
        ...prevState,
        outOfOfficeUser: val 
      }));
    })
  }, []);

  const handleSubmit = async (e) => {
   
    e.preventDefault();
    const formattedDetails = `
    Out Of Office User: ${formData.outOfOfficeUser}
    Surrogate User: ${formData.surrogateUser}
    Start Date: ${formData.startDate}
    End Date: ${formData.endDate}`;
    
    const getSpaceCsrfToken = async (credentials) => {
      return await fetchCsrfToken(credentials);
    };


    await fetchSwymCsrfToken(widget.getValue("Credentials"))
      .then(async (csrfToken) => {       
        const headers = {
          "Content-Type": "application/json",
          "Accept":"application/json",
          "x-requested-with": "XMLHttpRequest",
          ...csrfToken
        };
        const postdata = {
          "params" : {
            "user_infos": formattedDetails
          }         
        };
       
        fetch("https://"+sandbox+"-us1-3dswym.3dexperience.3ds.com/api/user/saveuserinfos", {
          "headers": headers,  
          "body":JSON.stringify(postdata) ,
          "method": "POST",
          "credentials": "include"
        }).then(async (response) => {
          console.log(response);            
          if(response.status === 200){
            showSuccessToast("Updated User About Details");  
            const spaceCsrfToken = await getSpaceCsrfToken(widget.getValue("Credentials"));
              const headers = {
                "Content-Type": "application/json",
                ...spaceCsrfToken
              };
            await callwebService('GET', `${tenant}/resources/modeler/pno/person?pattern=${formData.surrogateUser}`, "", headers)
            .then(async (personResp) => {
              console.log(personResp);
              const delegate_user_id = personResp.output.persons[0].pid;
              await callwebService('GET', `${tenant}/resources/v1/modeler/tasks/`, "", headers)
              .then((resp1) => {
        
                  console.log(resp1);    
                  const ids = resp1.output.data.map(item => item.id);
                  ids.forEach( async id => {                    
                      
                        const headers = {
                          "Content-Type": "application/json",
                          ...spaceCsrfToken
                        };
                        const data = {
                          "data": [
                               {
                                   "id": delegate_user_id,
                                   "type": "Person"
                               }
                           ]
                               };
                        await callwebService('POST', `${tenant}/resources/v1/modeler/tasks/`+id+'/assignees', JSON.stringify(data), headers)
                        .then((resp2) => {                  
                            console.log(resp2);            
                           
                          });
              });
        
            });
          });

          }
          else
          showErrorToast("Unable to update About Form");
        });          
      });
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const formStyle = {
    maxWidth: '400px',
    margin: '40px auto',
    padding: '20px',
    boxShadow: '0 0 10px rgba(0,0,0,0.1)',
    borderRadius: '8px',
    backgroundColor: 'white'
  };

  const fieldStyle = {
    marginBottom: '15px'
  };

  const labelStyle = {
    display: 'block',
    marginBottom: '5px',
    fontWeight: 'bold'
  };

  const inputStyle = {
    width: '100%',
    padding: '8px',
    borderRadius: '4px',
    border: '1px solid #ccc',
    boxSizing: 'border-box'
  };

  const buttonStyle = {
    backgroundColor: '#0066cc',
    color: 'white',
    padding: '10px 20px',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    width: '100%',
    fontSize: '16px'
  };

  return (
    <div style={{ minHeight: '100vh', backgroundColor: '#f5f5f5', padding: '20px' }}>
      <form onSubmit={handleSubmit} style={formStyle}>
        <h2 style={{ textAlign: 'center', marginBottom: '20px' }}>Out of Office Form</h2>
        
        <div style={fieldStyle}>
          <label style={labelStyle} htmlFor="outOfOfficeUser">
            Out Of Office User
          </label>
          <input
            type="text"
            id="outOfOfficeUser"
            name="outOfOfficeUser"
            value={formData.outOfOfficeUser}
            onChange={handleChange}
            style={inputStyle}
            required
          />
        </div>

        <div style={fieldStyle}>
          <label style={labelStyle} htmlFor="surrogateUser">
            Surrogate User
          </label>
          <input
            type="text"
            id="surrogateUser"
            name="surrogateUser"
            value={formData.surrogateUser}
            onChange={handleChange}
            style={inputStyle}
            required
          />
        </div>

        <div style={fieldStyle}>
          <label style={labelStyle} htmlFor="startDate">
            Start Date
          </label>
          <input
            type="date"
            id="startDate"
            name="startDate"
            value={formData.startDate}
            onChange={handleChange}
            style={inputStyle}
            required
          />
        </div>

        <div style={fieldStyle}>
          <label style={labelStyle} htmlFor="endDate">
            End Date
          </label>
          <input
            type="date"
            id="endDate"
            name="endDate"
            value={formData.endDate}
            onChange={handleChange}
            style={inputStyle}
            required
          />
        </div>

        <button type="submit" style={buttonStyle}>
          Submit
        </button>
      </form>
    </div>
  );
}

export default OutOfOffice;